<template>
  <div class="goods">
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <div class="goods-top">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-input
              placeholder="请输入内容"
              v-model="query.query"
              clearable
              @clear="clear"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="search"
              ></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary"
              ><router-link to="/goods/add">添加商品</router-link></el-button
            >
          </el-col>
        </el-row>
      </div>
      <el-table :data="goods" stripe style="width: 100%" border>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="goods_name" label="商品名称" width="600">
        </el-table-column>
        <el-table-column prop="goods_price" label="商品价格(元)">
        </el-table-column>
        <el-table-column prop="goods_weight" label="商品重量">
        </el-table-column>
        <el-table-column prop="add_time" label="创建时间">
          <template v-slot="scope">
            {{ scope.row.add_time | moment("YYYY-MM-DD HH:MM:SS") }}
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template v-slot="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="edit(scope.row)"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="del(scope.row.goods_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        v-if="total"
        background
        :page-sizes="[5, 10, 15, 20]"
        :page-size="query.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @current-change="currentChange"
        @size-change="sizeChange"
      >
      </el-pagination>
    </el-card>
    <!-- 编辑框 -->
    <el-dialog title="编辑商品" :visible.sync="dialogVisible" width="50%">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="商品名称" prop="goods_name">
          <el-input v-model="ruleForm.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="goods_price">
          <el-input v-model="ruleForm.goods_price"></el-input>
        </el-form-item>
        <el-form-item label="数量" prop="goods_number">
          <el-input v-model="ruleForm.goods_number"></el-input>
        </el-form-item>
        <el-form-item label="重量" prop="goods_weight">
          <el-input v-model="ruleForm.goods_weight"></el-input>
        </el-form-item>
        <el-form-item label="商品分类">
          <el-cascader
            v-model="value"
            :options="categoryAllList"
            :props="props"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sendEdit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      query: {
        query: "",
        pagenum: 1,
        pagesize: 5,
      },
      dialogVisible: false,
      rules: {
        goods_name: {
          required: true,
          message: "请输入商品名称",
          trigger: "blur",
        },
        goods_price: {
          required: true,
          message: "请输入商品价格",
          trigger: "blur",
        },
        goods_number: {
          required: true,
          message: "请输入商品数量",
          trigger: "blur",
        },
        goods_weight: {
          required: true,
          message: "请输入商品重量",
          trigger: "blur",
        },
      },
      ruleForm: {},
      goodsId: "",
      props: {
        label: "cat_name",
        value: "cat_id",
        expandTrigger: "hover",
        checkStrictly: true,
      },
      value: [],
    };
  },
  created() {
    this.getGoods(this.query);
    this.getGoodsCategory({ type: 3 });
  },
  computed: {
    ...mapState({
      goods: (state) => state.goods.goods,
      total: (state) => state.goods.total,
      categoryAllList: (state) => state.goodsCategory.categoryAllList,
    }),
  },
  methods: {
    // 更改当前页
    currentChange(value) {
      this.query.pagenum = value;
      this.getGoods(this.query);
    },
    // 更改页的展示数量
    sizeChange(value) {
      this.query.pagesize = value;
      this.getGoods(this.query);
    },
    // 搜索
    search() {
      this.getGoods(this.query);
    },
    // 清空搜索框
    clear() {
      this.getGoods(this.query);
    },
    // 添加商品
    add() {},
    // 编辑商品
    edit(row) {
      // console.log(row);
      this.dialogVisible = true;
      this.goodsId = row.goods_id;
      this.ruleForm = { ...row };
    },
    // 提交编辑
    sendEdit() {
      // console.log(this.ruleForm);
      this.ruleForm.goods_cat = this.value.join(",");
      this.editGoodsItem({
        id: this.goodsId,
        data: this.ruleForm,
      }).then(
        (res) => {
          this.$message.success("修改成功");
        },
        (error) => {
          this.$message.error(error);
        }
      );
      this.dialogVisible = false;
    },
    // 删除
    del(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.delGoodsItem(id).then(
            (res) => {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
            },
            (error) => {
              this.$message({
                type: "error",
                message: error,
              });
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    ...mapActions([
      "getGoods",
      "editGoodsItem",
      "delGoodsItem",
      "getGoodsCategory",
    ]),
  },
};
</script>

<style lang="less">
.goods {
  .el-breadcrumb {
    font-size: 12px;
    margin-bottom: 20px;
  }
  .el-table {
    margin: 15px 0;
    font-size: 12px;
  }
  a {
    color: #fff;
    text-decoration: none;
  }
}
.el-cascader-menu {
  height: 200px;
}
</style>
